@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap");

:root {
  --main-color: var(--main-color);
}
html {
  scroll-behavior: smooth;
}
* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  line-height: 1.5;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  background: linear-gradient(to top, #09203f 0%, #537895 100%) no-repeat center
    center fixed;
}

body {
  color: white;
  padding-right: 30rem;
}

section {
  padding: 1rem 5%;
  min-height: 100vh;
}

input[type="color"] {
  display: none;
}
.custom-color-upload{
  padding: 2px 7px;
  color: inherit;
  font-size: 2rem;
  z-index: 999;
  position: fixed;
  display: block;
  margin: 12px;
  left: 4px;
  bottom: 4px;
  background-color: var(--main-color);
  border-radius: 22px;
  border: 1px solid white;
  box-shadow: 0 0 12px rgb(210, 210, 210);
}
.heading {
  animation: floaterxr 1s;
  font-size: 4rem;
  padding-bottom: 1rem;
  color: #ffffff;
  text-transform: uppercase;
}

.heading span {
  color: var(--main-color);
  text-transform: uppercase;
}

.btn {
  transition: all 0.2s linear;
  display: inline-block;
  margin-top: 1rem;
  padding: 0.8rem 3rem;
  background: var(--main-color);
  color: #fff;
  cursor: pointer;
  font-size: 1.7rem;
  border-radius: 12px;
  margin: 0 5px;
}

.btn:hover {
  transition: all 0.2s linear;
  background: #111;
  letter-spacing: 0.2rem;
}

.headincolor{
  border-radius: 100px;
  border: none;
}

header {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.276);
  backdrop-filter: blur(22px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  flex-flow: column;
  padding: 8rem 2rem;
  width: 30rem;
  text-align: center;
  border-left: 1px solid rgb(0, 0, 0);
  box-shadow: 0 0 12px black;
}

header .logo {
  text-transform: uppercase;
  color: #fff;
  font-size: 3rem;
  font-weight: bolder;
}

header .logo span {
  text-transform: uppercase;
  color: var(--main-color);
}

header .navbar a {
  transition: all 0.2s linear;
  display: block;
  font-size: 2rem;
  color: #fff;
  margin: 2rem 0;
}

header .navbar a:hover {
  transition: all 0.2s linear;
  letter-spacing: 0.2rem;
  color: var(--main-color);
}

header .follow a {
  font-size: 2.5rem;
  color: #fff;
  margin: 0 0.7rem;
}

header .follow a:hover {
  color: var(--main-color);
}

#menu-bars {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 10000;
  background: var(--main-color);
  color: #fff;
  border-radius: 0.5rem;
  padding: 1rem 1.5rem;
  font-size: 3rem;
  cursor: pointer;
  display: none;
}


.home {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.home .image {
  flex: 1 1 40rem;
}

.home .image img {
  animation: floatery 0.5s;
  width: 100%;
}

.home .content {
  animation: floaterxr 1s;
  flex: 1 1 52rem;
}

.home .content .hi {
  font-size: 3rem;
  color: var(--main-color);
}

.home .content h3 {
  font-size: 5.5rem;
  text-transform: uppercase;
}

.home .content h3 span {
  color: var(--main-color);
  text-transform: uppercase;
}

.home .content .info {
  font-size: 3.5rem;
  padding: 0.5rem 0;
}

.home .content .text {
  font-size: 2.7rem;
  color: rgb(216, 216, 216);
  padding: 0.5rem 0;
}

.about .row-1 {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  padding-bottom: 2rem;
}

.about .row-1 .image {
  animation: floaterxr 1s;
  flex: 1 1 25rem;
}

.about .row-1 .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border: 1rem solid #fff;
  border-radius: 0.5rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}

.about .row-1 .content {
  flex: 1 1 50rem;
}

.about .row-1 .content h3 {
  animation: floaterx 1s;
  color: #ffffff;
  font-size: 3.5rem;
}

.about .row-1 .content p {
  animation: floaterx 1.5s;
  color: rgb(255, 252, 234)9e9;
  font-size: 1.7rem;
  padding: 0.5rem 0;
}

.about .row-1 .content .box-container {
  animation: floaterx 2.3s;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  padding: 0.5rem 0;
}

.about .row-1 .content .box-container .box {
  flex: 1 1 20rem;
}

.about .row-1 .content .box-container .box span {
  color: var(--main-color);
}

.about .row-2 {
  animation: floatery 1s;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: center;
}

.about .row-2 .skills {
  flex: 1 1 40rem;
}

.about .row-2 .skills .progress {
  padding: 1rem 0;
}

.about .row-2 .skills .progress h3 {
  display: flex;
  justify-content: space-between;
  padding: 0.7rem 0;
  font-size: 1.7rem;
  color: #ffd950;
  font-weight: 400;
}

.about .row-2 .skills .progress .bar {
  width: 100%;
  background: #cccccc;
  overflow: hidden;
  height: 1rem;
  border-radius: 50rem;
}

.about .row-2 .skills .progress .bar span {
  display: block;
  height: 100%;
  background: var(--main-color);
  border-radius: 50rem;
}

.about .row-2 .skills .progress:nth-child(1) .bar span {
  width: 95%;
}

.about .row-2 .skills .progress:nth-child(2) .bar span {
  width: 80%;
}

.about .row-2 .skills .progress:nth-child(3) .bar span {
  width: 75%;
}

.about .row-2 .skills .progress:nth-child(4) .bar span {
  width: 85%;
}

.about .row-2 .box-container {
  flex: 1 1 40rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.about .row-2 .box-container .box {
  flex: 1 1 15rem;
  padding: 2rem;
  background-image: linear-gradient(45deg, #a1c4fd 0%, #c2e9fb 100%);
  backdrop-filter: blur(22px);
  border-radius: 0.5rem;
  box-shadow: 0 0.5rem 1rem rgba(255, 187, 16, 0.339);
}

.about .row-2 .box-container .box h3 {
  color: var(--main-color);
  font-size: 3rem;
}

.about .row-2 .box-container .box p {
  color: #280033;
  font-size: 1.7rem;
  padding-top: 0.5rem;
}

.service .box-container {
  animation: floatery 1s;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.service .box-container .box {
  flex: 1 1 30rem;
  text-align: center;
  padding: 2rem;
  background-image: linear-gradient(45deg, #a1c4fd 0%, #c2e9fb 100%);
  backdrop-filter: blur(22px);
  border-radius: 0.5rem;
  box-shadow: 0 0.5rem 1rem rgba(184, 184, 184, 0.638);
}

.service .box-container .box i {
  height: 6rem;
  width: 6rem;
  line-height: 6rem;
  font-size: 2.5rem;
  color: #fff;
  background: var(--main-color);
  border-radius: 50%;
}

.service .box-container .box h3 {
  color: #000000;
  font-size: 2.2rem;
  padding-top: 1rem;
}

.service .box-container .box p {
  color: #1f003e;
  font-size: 1.7rem;
  padding-top: 0.5rem;
}

.experience .box-container {
  display: flex;
  flex-wrap: wrap;
}

.experience .box-container .box {
  animation: floaterx 1s;
  position: relative;
  flex: 1 1 33rem;
  border-left: 0.2rem solid var(--main-color);
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 2rem;
}

.experience .box-container .box::before {
  content: "";
  position: absolute;
  top: 0;
  left: -1rem;
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  background: var(--main-color);
}

.experience .box-container .box .content {
  background-image: linear-gradient(45deg, #a1c4fd 0%, #c2e9fb 100%);
  backdrop-filter: blur(22px);
  padding: 1.5rem;
  box-shadow: 0 0.5rem 1rem rgba(215, 215, 215, 0.414);
  border-radius: 0.5rem;
}

.experience .box-container .box .content span {
  color: #fff;
  font-size: 1.7rem;
  background: var(--main-color);
  border-radius: 50rem;
  padding: 0.4rem 1rem;
}

.experience .box-container .box .content h3 {
  font-size: 2.5rem;
  padding: 0.5rem 0;
  color: #111;
}

.experience .box-container .box .content p {
  font-size: 1.6rem;
  color: #00062e;
}
.portfolio .box-container {
  animation: floatery 1s;
  transition: all 0.2s linear;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.portfolio .box-container .box {
  transition: all 0.2s linear;
  flex: 1 1 30rem;
  height: 25rem;
  position: relative;
  overflow: hidden;
  border: 0.8rem solid #00e022;
  box-shadow: 0 0.5rem 1rem rgba(160, 160, 160, 0.406);
  background-image: linear-gradient(45deg, #a1c4fd 0%, #c2e9fb 100%);
  backdrop-filter: blur(22px);
  border-radius: 0.5rem;
}

.portfolio .box-container .box img {
  transition: all 0.2s linear;
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 0.5rem;
  position: relative;
  z-index: 1;
}

.portfolio .box-container .box:hover img {
  transition: all 0.2s linear;
  height: 75%;
  width: 75%;
}

.portfolio .box-container .box h3 {
  position: absolute;
  bottom: 0.5rem;
  left: 1rem;
  font-size: 2.5rem;
  color: #000000;
  text-transform: uppercase;
}

.portfolio .box-container .box .icons {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.portfolio .box-container .box .icons a {
  display: block;
  height: 4rem;
  width: 4rem;
  border-radius: 0.5rem;
  background: #eee;
  color: #333;
  font-size: 1.7rem;
  line-height: 4rem;
  text-align: center;
  margin-bottom: 1rem;
}

.portfolio .box-container .box .icons a:hover {
  background: var(--main-color);
  color: #ffffff;
}

.contact .icons-container {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
  padding-bottom: 2rem;
}

.contact .icons-container .icons {
  animation: floaterx 1s;
  flex: 1 1 25rem;
  text-align: center;
  padding: 2rem;
  background-image: linear-gradient(45deg, #a1c4fd 0%, #c2e9fb 100%);
  backdrop-filter: blur(22px);
  box-shadow: 0 0.5rem 1rem rgba(187, 187, 187, 0.357);
  border-radius: 0.5rem;
}

.contact .icons-container .icons i {
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
  font-size: 2rem;
  background: var(--main-color);
  color: #fff;
  border-radius: 50%;
}

.contact .icons-container .icons h3 {
  color: #000000;
  font-size: 2.3rem;
  padding: 0.5rem 0;
  padding-top: 1rem;
}

.contact .icons-container .icons p {
  color: #000341;
  font-size: 1.7rem;
}

.contact .row {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.contact .row form {
  animation: floatery 1s;
  flex: 1 1 55rem;
  background: #ffffff;
  padding: 0 2rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
}

.contact .row .map {
  animation: floaterx 2s;
  flex: 1 1 25rem;
  border: 1rem solid #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  width: 100%;
}

.contact .row form .box,
.contact .row form textarea {
  width: 100%;
  border-bottom: 0.1rem solid #333;
  color: #333;
  text-transform: none;
  font-size: 1.7rem;
  padding: 1rem 0;
  margin: 1rem 0;
}

.contact .row form .box:focus,
.contact .row form textarea:focus {
  border-color: var(--main-color);
}

.contact .row form .box::placeholder,
.contact .row form textarea::placeholder {
  text-transform: capitalize;
}

.contact .row form textarea {
  height: 20rem;
  resize: none;
}

.contact .row form .btn {
  transition: all 0.2s linear;
  margin-top: 0;
  margin-bottom: 1.5rem;
}

.footer {
  margin-top: 2rem;
  font-size: 2rem;
  padding: 2.5rem;
  text-align: center;
  color: #ffc848;
  background-image: linear-gradient(45deg, #a1c4fd2e 0%, #c2e9fb22 100%);
  backdrop-filter: blur(22px);
}

.footer span {
  color: var(--main-color);
}

/* media queries  */
@media (max-width: 991px) {
  html {
    font-size: 55%;
  }

  body {
    padding: 0;
  }

  #menu-bars {
    display: initial;
  }

  header {
    left: -120%;
  }

  header.active {
    left: 0%;
  }

  .cursor-1,
  .cursor-2 {
    display: none;
  }
}

@media (max-width: 450px) {
  html {
    font-size: 50%;
  }

  header {
    width: 100%;
  }

  .experience .box-container .box {
    padding-right: 0;
  }
}
